.nav {
    grid-area: nav;
    background-color: darksalmon;
}

.navItem {
    margin: 5px 10px;
}

.navItem a{
    color: white;
    text-decoration: none;
    font-size: 20px;
}

.navItem a:hover{
    color: brown;
    text-decoration: none;
    font-size: 25px;
}

.navItem a.active{
    color: chocolate;
}

.navItem:last-child {
    margin-top: 30px;
}

.topFriends {
display: flex;
}

.friend {
    margin: 5px;
    text-align: center;
}

.friend img{
width: 30px;
    border-radius: 30px;
}